/**
 * Select2 Finnish translation
 */
(function ($) {
    "use strict";
    $.fn.select2.locales['fi'] = {
        formatNoMatches: function () {
            return "Ei tuloksia";
        },
        formatInputTooShort: function (input, min) {
            var n = min - input.length;
            return "Ole hyv ja anna " + n + " merkki lis";
        },
        formatInputTooLong: function (input, max) {
            var n = input.length - max;
            return "Ole hyv ja anna " + n + " merkki vhemmn";
        },
        formatSelectionTooBig: function (limit) {
            return "Voit valita ainoastaan " + limit + " kpl";
        },
        formatLoadMore: function (pageNumber) {
            return "Ladataan lis tuloksia";
        },
        formatSearching: function () {
            return "Etsitn";
        }
    };

    $.extend($.fn.select2.defaults, $.fn.select2.locales['fi']);
})(jQuery);

define("select2-origin/select2_locale_fi", function(){});

